
/* eslint-disable object-curly-newline */
import { Form as vFrom, Field, configure, defineRule, ErrorMessage } from 'vee-validate';
import { required, email, min } from '@vee-validate/rules';
/* eslint-enable  */
import { localize } from '@vee-validate/i18n';
import zhTW from '@vee-validate/i18n/dist/locale/zh_TW.json';

import Breadcrumb from '../../components/cart/Breadcrumb.vue';

configure({
  generateMessage: localize('zh_TW', zhTW),
});

defineRule('email', email);
defineRule('required', required);
defineRule('min', min);

export default {
  components: {
    vFrom,
    Field,
    ErrorMessage,
    Breadcrumb,
  },
  data() {
    return {
      order: {
        name: '',
        email: '',
        phone: null,
        address: '',
        message: '',
      },
    };
  },
  methods: {
    phoneNumber(value) {
      const phoneNumber = /^(09)[0-9]{8}$/;
      return phoneNumber.test(value) ? true : '';
    },
    onSubmit() {
      const data = {
        ...this.order,
      };
      this.$store.dispatch('order/orderInformation', data);
      this.$router.push('/orderCheck');
    },
    editOrder() {
      const order = this.$store.getters['order/orderInformation'];
      if (order.name !== '' && order.name !== undefined) {
        this.order = order;
      }
    },
    orderClear() {
      this.order.name = '';
      this.order.email = '';
      this.order.phone = null;
      this.order.address = '';
      this.order.message = '';
    },
  },
  mounted() {
    this.editOrder();
  },
  unmounted() {
    this.orderClear();
  },
};
